<style>
  .medio{
    color: black;
  }
  .cube-tab-bar-slider{
    background: #f01414;
  }
  .cube-tab_active{
    color: #f01414;
  }
  .medio .cube-tab{
    padding: 10px 0;
  }
  .scroll-wrap{
    height: 76vh;
  }
  .cube-scroll-nav-bar-item{
    display: flex !important;
    justify-content: center;
    flex-wrap: wrap;
    width: 15vw;
    align-items: center;
    background: #f3f5f7;
    height: 5vw;
  }
  .cube-sticky,
  .cube-scroll-nav-bar-items{
    padding-bottom: 10vh
  }
  .cube-scroll-nav-bar-items span{
    white-space: normal;
    font-size: 13px;
    line-height: 14px;
    color: #333;
  }
  .cube-scroll-nav-bar-item_active{
    background: white;
  }
  .cube-scroll-nav-panel-title{
    background: #f3f5f7;
    border-left: 2px solid #d9dde1;
    padding-left: 14px;
    font-size: 14px;
    height: 26px;
    line-height: 26px;
  }
  .medio .comdty{
    position: relative;
    margin: 15px;
    padding-bottom: 10px;
    display: flex;
    height: 60px;
    border-bottom: 1px solid #dddddd
  }
  .medio .comdty>img{
    width: 20vw;
    margin-right: 10px;
  }
  .medio .details{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  .medio .details>span{
    color: #999;
  }
  .medio .details>i{
    font-style: normal;
    color: red;
  }
  .medio .add{
    width: 70px;
    height: 20px;
    position: absolute;
    bottom: 10px;
    right: 0px;
  }
  .medio .add>img{
    position: absolute;
    width: 20px;
    right: 0px;
  }
  .medio .add>p{
    width: 100%;
    height: 100%;
    line-height: 20px;
    text-align: center;
  }
  .medio .add>.jia{
    z-index: 1
  }
  .medio .add>.jian{
    transition:all .5s cubic-bezier(0.175, 0.885, 0.32, 1.275);
    opacity: 0;
    transform: translateX(0px) rotateZ(0deg);
  }
  .medio .add>.jian.active{
    transition:all .5s cubic-bezier(0.175, 0.885, 0.32, 1.275);
    transform: translateX(-50px) rotateZ(-360deg);
    opacity: 1;
  }
</style>
<template>
  <div class="medio">
    <cube-tab-bar
      v-model="selectTab"
      :data="tabs"
      :use-transition="disabled"
      :show-slider="showSlider"
      ref="tabNav"
      @click="clickHandler"
    >
    </cube-tab-bar>
    <cube-slide
      ref="slide"
      :loop="loop"
      :show-dots="showDots"
      :auto-play="autoPlay"
      :initial-index="page"
      :options="options"
      :stopPropagation="stopPropagation"
      @scroll="scroll"
      @change="changePage"
      >
      <cube-slide-item>
        <div class="scroll-wrap">
          <cube-scroll-nav
            :side="true"
            :data="data"
            :current="current"
            @sticky-change="stickyChangeHandler">
            <cube-scroll-nav-panel
              v-for="(item,i) in data"
              :key="i"
              :label="item.name"
              :title="item.name">
              <ul @click="add" :data-ul="i">
                <!-- 商品栏 -->
                <li v-for="(food,i) in item.foods" :key="i" :data-index="i">
                  <div class="comdty">
                    <img :src="food.icon">
                    <div class="details">
                      <h2>{{food.name}}</h2>
                      <span>{{food.type}}</span>
                      <span>月售{{food.yueshou}} 好评率{{food.ping}}</span>
                      <i>￥{{food.price}}</i>
                    </div>
                    <div class="add">
                      <img class="jia" src="../assets/img/jia.png">
                      <img class="jian" src="../assets/img/jian.png">
                      <p></p>
                    </div>
                  </div>
                </li>
              </ul>
            </cube-scroll-nav-panel>
          </cube-scroll-nav>
        </div>
      </cube-slide-item>
      <cube-slide-item>
        <div class="scroll-wrap">
          <pinglun></pinglun>
        </div>
      </cube-slide-item>
      <cube-slide-item :stopPropagation="stopPropagation">
        <div class="scroll-wrap">
          <Mydetails></Mydetails>
        </div>
      </cube-slide-item>
    </cube-slide>

    <car></car>
  </div>
</template>

<script>
import {data} from './data.js'
import car from './car'
import pinglun from './pinglun'
import Mydetails from './details'
export default {
  components: {
    car,
    pinglun,
    Mydetails
  },
  data(){
    return {
      showSlider:true,
      selectTab: "商品",
      disabled: false, //底部导航条过渡效果
      tabs: [{
        label: '商品',
        index:0
      }, {
        label: '评论',
        index:1
      }, {
        label: '商家',
        index:2
      }],
      stopPropagation: true,
      loop: false,  // 自动播放
      showDots: false,  //点
      autoPlay: false,  //自动播放
      page: 0,   // 页
      options: {
        listenScroll: true,  //实时滚动位置
        probeType: 3,  //实时位置
        // directionLockThreshold: 0,
        scrollY: false,
        stopPropagation: true
      },
      data: null,  //所有商品
      current: "热销榜",
      value: 4.2,  //评分
      showAdd: true,    //显示减号
      fangdou: 1,    //click防抖
    }
  },
  methods:{

    add(e){   //小球滚动处理函数
      if(this.fangdou){
        // 防抖：
        this.fangdou = 0
        // 处理任务：
        // 加号：
        if(e.target.className == 'jia'){
          const typeIndex = e.path[4].dataset.ul  //所属分了下标
          const index = e.path[3].dataset.index   //所属分类内的商品下标
          const comdty = this.data[typeIndex].foods[index] //当前所点击的商品
          const jian = e.target.parentNode.childNodes[1]   //减号
          const count = e.target.parentNode.childNodes[2]  //数量
          if(count.innerText == ""){
            count.innerText = 1
          }else{
            count.innerText++
          }
          jian.className = 'jian active'
          comdty.count = count.innerText;
          this.$store.commit("addComdty",comdty)
        }
        // 减号：
        if(e.target.className == 'jian active'){
          const typeIndex = e.path[4].dataset.ul  //所属分了下标
          const index = e.path[3].dataset.index   //所属分类内的商品下标
          const comdty = this.data[typeIndex].foods[index] //当前所点击的商品
          const count = e.target.parentNode.childNodes[2]
          if(count.innerText > 1){
            count.innerText--
          }else{
            count.innerText = ""
            e.target.className = 'jian'
          }
          comdty.count = count.innerText;
          this.$store.commit("subComdty",comdty)
        }

        setTimeout(()=>{
          this.fangdou = 1
        },300)
      }
    },
    clickHandler (label) {
      this.tabs.forEach((item,i)=>{
        if(label == item.label){
          this.page = item.index
          return
        }
      })
    },
    changePage(current){
      console.log(current)
      this.selectTab = this.tabs[current].label
    },
    scroll (pos) {
      const x = Math.abs(pos.x)
      const tabItemWidth = this.$refs.tabNav.$el.clientWidth
      const slideScrollerWidth = this.$refs.slide.slide.scrollerWidth
      const deltaX = x / slideScrollerWidth * tabItemWidth
      this.$refs.tabNav.setSliderTransform(deltaX)
    },
    stickyChangeHandler(current){
      console.log(current)
    }
  },
  created(){
    // 为商品添加id属性
    var id = 0
    data.forEach(function(item,i,arr){
      item.foods.forEach(function(item1,i1,arr1){
        item1.id = id
        id++
      })
    })
    this.data = data
  }
}
</script>