import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    comdtys: []
  },
  mutations: {
    addComdty(state,comdty){
      if(state.comdtys.length == 0){
        state.comdtys.push(comdty)
      }else{
        for(var i=0;i<state.comdtys.length;i++){
          // 如果商品id相同：
          if(state.comdtys[i].id == comdty.id){
            state.comdtys.splice(i,1,comdty)
            return
          }else{
            if(i == state.comdtys.length-1){
              state.comdtys.push(comdty)
              break
            }
          }
        }
      }
    },
    subComdty(state,comdty){
      for(var i=0;i<state.comdtys.length;i++){
        // 如果商品id相同：
        if(state.comdtys[i].id == comdty.id){
          if(comdty.count == 0){
            state.comdtys.splice(i,1)
            return
          }
          state.comdtys.splice(i,1,comdty)
          return
        }
      }
    }
  },
  actions: {
  },
  modules: {
  }
})
