<style lang="stylus">
    .car
        position fixed
        background #07111b
        height 7.5vh
        width 100%
        bottom 0
        display flex
        color rgba(255, 255, 255, 0.4)
        .left
            width 70%
            display flex
        .left>div.car
            display flex
            width 56px
            height 56px
            transform translateY(-11px) translateX(12px)
            box-sizing border-box
            border-radius 50%
            border 6px solid #141d27
            align-items center
            justify-content center
            background #00a0dc
            position relative
        .left>div.price
            padding-left 25px
            font-size 16px
            font-weight 700
            line-height 50px
        .left .count
            background red
            position absolute
            width 24px
            height 16px
            border-radius 16px
            font-size 12px
            line-height 16px
            text-align center
            color white
            box-shadow 0 4px 8px 0 rgba(0,0,0,0.4)
            top -5px
            right -6px
        .right 
            width 30%
            text-align center
            line-height 7.5vh
</style>

<template>
    <div class="car">
        <div class="left">
            <div class="car" :style="price > 0?'background:#00a0dc;':'background:#2b343c;'">
                <img src="../assets/img/car.png">
                <div :style="count>0?'display:block;':'display:none;'" class="count">{{count}}</div>
            </div>
            <div class="price" :style="price > 0?'color:white;':''">
                ￥{{price}} 元
            </div>
        </div>
        <div class="right" 
            :style="price >= 20?'background:#00b43c;color:white;'
            :'background:#2b333b;color:rgba(255,255,255,0.4);'"
            >
            {{price | priceChange}}
        </div>
    </div>
</template>

<script>
export default {
    data(){
        return {
        }
    },
    filters: {
        priceChange(num){
            if(num == 0){
                return '￥20元起送'
            }
            else if(num >0 && num<20){
                return ('还差￥'+(20-num)+'元起送')
            }else{
                return '去结算'
            }
        }
    },
    computed: {
        price(){
            if(this.$store.state.comdtys.lenght==0){
                return 0
            }else{
                const price=this.$store.state.comdtys.reduce(function(prev,item,i){
                    prev+=item.price*item.count
                    return prev
                },0)
                return price
            }
        },
        count(){
            if(this.$store.state.comdtys.lenght==0){
                return 0
            }else{
                const count=this.$store.state.comdtys.reduce(function(prev,item,i){
                    prev+=Number(item.count)
                    return prev
                },0)
                return count
            }
        }
    }
}
</script>